
import trailList from '../trailList.js';

function Trail(props) {
 
 
  return (
    <div className="sub-wrap">
        <div className="sub-hero">
          <img src={`${process.env.PUBLIC_URL}/imgs/subhero.jpg`} alt="activity" className=""/>
        </div>
        <div className="sub-item">   
            <h2>WEEKLY BEST ITEM</h2>
              {trailList.map((a, index) => (
                <div key={index}> 
                  <div>
                    <p className="pt-3 mb-1">{a.title}</p>
                    <p className="bf6">{a.price}</p>
                    <p className="pb-1 ">{a.desc}</p>
                    <div className="pb-1 "><img>{a.timg}</img></div>
                  </div>
                </div>
              ))}

        </div>
    </div>
  );
}



export default Trail;
